/* Global styles and variables */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500&family=Roboto:wght@500&display=swap");

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root {
    --primary-text-color: #183b56;
    --secondary-text-color: #577592;
    --accent-color: #2294ed;
    --accent-color-dark: #1d69a3;
}

body {
    font-family: "Poppins", sans-serif;
    color: var(--primary-text-color);
    margin: 0;
    padding: 0;
}

p {
    font-family: "Roboto", sans-serif;
    color: var(--secondary-text-color);
    line-height: 1.4rem;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

.flex {
    display: flex;
    align-items: center;
}

.container {
    max-width: 1180px;
    margin: 0 auto; /* Center align container */
    padding: 0 20px; /* Add padding on the sides */
    overflow: hidden;
}

nav {
    background-color: #f3faff;
    box-shadow: 0 0 4px #bbd0e2;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 99;
}

.main-nav {
    justify-content: space-between;
    padding: 8px 0; /* Adjust vertical padding */
}

.company-logo img {
    width: 100px; /* Adjust logo size */
    height: auto;
}

.nav-toggle {
    display: none; /* Hide toggle button by default */
    cursor: pointer;
}

.nav-links {
    display: flex;
    align-items: center;
    gap: 16px;
}

.hover-link {
    cursor: pointer;
    transition: color 0.3s ease;
}

.hover-link:hover {
    color: var(--secondary-text-color);
}

.hover-link:active {
    color: red;
}

.nav-item.active {
    color: var(--accent-color);
}

.search-bar {
    display: flex;
    align-items: center;
    gap: 8px;
}

.news-input {
    width: 100%; /* Occupy full width */
    max-width: 300px; /* Limit maximum width */
    padding: 12px;
    border-radius: 4px;
    border: 2px solid #bbd0e2;
    font-family: "Roboto", sans-serif;
}

.search-button {
    background-color: var(--accent-color);
    color: white;
    padding: 10px 20px; /* Increase padding */
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-family: "Roboto", sans-serif;
}

.search-button:hover {
    background-color: var(--accent-color-dark);
}

main {
    padding: 20px;
    margin-top: 80px;
}

.cards-container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 20px;
}

.card {
    width: calc(33.33% - 20px); /* Adjust card width for responsive layout */
    min-height: 400px;
    box-shadow: 0 0 4px #d4ecff;
    border-radius: 4px;
    cursor: pointer;
    background-color: #fff;
    overflow: hidden;
    transition: all 0.3s ease;
}

.card:hover {
    box-shadow: 1px 1px 8px #d4ecff;
    background-color: #f9fdff;
    transform: translateY(-2px);
}

.card-header img {
    width: 100%;
    height: 180px;
    object-fit: cover;
}

.card-content {
    padding: 12px;
}

.news-source {
    margin-top: 12px; /* Adjust spacing */
    color: var(--secondary-text-color);
    font-size: 14px; /* Adjust font size */
}

/* Media Queries for Responsive Design */

@media (max-width: 768px) {
    .company-logo img {
        width: 80px; /* Adjust logo size for smaller screens */
    }

    .nav-toggle {
        display: block; /* Show toggle button for mobile */
        margin-left: auto; /* Align to the right */
        cursor: pointer;
    }

    .nav-links {
        flex-direction: column;
        position: absolute;
        top: 64px; /* Adjust top position */
        left: 0;
        right: 0;
        background-color: #f3faff;
        box-shadow: 0 0 4px #bbd0e2;
        padding: 16px;
        transform: translateY(-100%);
        transition: transform 0.3s ease;
        z-index: -1; /* Initially hidden behind the navbar */
        opacity: 0;
        pointer-events: none; /* Disable click events */
      }

    .nav-links.active {
        transform: translateY(0);
        opacity: 1;
        pointer-events: auto; /* Enable click events */
        z-index: 99; /* Bring menu above the content */
      }

    .nav-links ul {
        flex-direction: column;
        gap: 12px;
    }

    .nav-item {
        padding: 12px;
        font-size: 16px; /* Adjust font size for smaller screens */
    }

    .search-bar {
        flex-direction: column;
        align-items: flex-start;
    }

    .news-input {
        max-width: none; /* Allow input to occupy full width */
    }

    .cards-container {
        margin-top: 120px; /* Adjust margin-top for card container */
    }

    .card {
        width: calc(50% - 20px); /* Adjust card width for two columns on smaller screens */
    }
}

@media (max-width: 480px) {
    .card {
        width: 100%; /* Full width for single column on smaller screens */
    }
}
